<template>
  <multiselect
    v-model="selectedTerrain"
    track-by="name_en"
    label="name_en"
    placeholder="Select one"
    selectLabel=""
    deselectLabel=""
    :value="selectedTerrain"
    :options="terrains"
    :searchable="false"
    :allow-empty="false"
    :clear-on-select="false"
    :maxHeight="550"
  >
    <template
      v-if="selectedTerrain.id"
      slot="singleLabel"
      slot-scope="{ option }"
    >
      <div class="selected">
        <img :src="`/images/terrains/${selectedTerrain.name_en}.webp`" />
        <span class="multiselect__selected">
          {{ option.name_en }}
        </span>
      </div>
    </template>

    <template slot="option" slot-scope="{ option }">
      <div class="options">
        <img :src="`/images/terrains/${option.name_en}.webp`" />
        <span>{{ option.name_en }}</span>
      </div>
    </template>
  </multiselect>
</template>

<script>
import Multiselect from "vue-multiselect";

import "vue-multiselect/dist/vue-multiselect.min.css";

export default {
  components: {
    Multiselect
  },
  props: {
    terrains: {
      value: Array,
      default: [],
      required: false
    }
  },
  data() {
    return {
      selectedTerrain: {}
    };
  },
  methods: {
    onSelect() {
      this.$emit("select", this.selectedTerrain);
    }
  }
};
</script>

<style lang="scss" scoped>
.multiselect {
  height: 40px;
  max-width: 300px;
}

.selected,
.options {
  display: flex;
  align-items: center;
  font-size: 1.125rem;

  img {
    width: 80px;
    height: auto;
    margin-right: 10px;
  }
}
</style>
